import React, {useEffect, useRef, useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import HtmlToReact from 'html-to-react';
import * as ARBApi from '../../utils/ARBApi'

const useStyles = makeStyles((theme) => ({
  title: {
    backgroundColor: "#FFFFFF",
    color: "#082D68"
  }
}));

export default function MatchReport({open, handleClose, title, report, played, opponentTeamId}) {
  const classes = useStyles();
  const descriptionElementRef = useRef(null);
  const [matchesWithTeam, setMatcheWithTeam] = useState([]);
  
  useEffect(() => {
    if (open) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
    if (played==='N'){
      ARBApi.getAllByID(`teams`, opponentTeamId).then((matchesWithTeam) => {
        console.log(matchesWithTeam);
        setMatcheWithTeam(matchesWithTeam);
      });
    }
  }, [open, played, opponentTeamId]);
  
  if (matchesWithTeam.length>0){
    let stats = {
      "v": 0,
      "p": 0,
      "s": 0,      
    }
    matchesWithTeam.forEach( (match) => {
      console.log(match);
      if (match.gf===match.gc) {
        stats.p += 1;
      }
      else if (match.gc > match.gf && match.home==="Cagliari") {
        stats.v += 1;
      } else {
        stats.s += 1;
      }
    }, [stats]);
    console.log("stats", stats);
    report = `<div><ul><li>Vinte: ${stats.v}</li><li>Pareggiate: ${stats.s}</li><li>Sconfitte: ${stats.s}</li><li>Totale: ${matchesWithTeam.length}</li></ul></div>`;
    title = `Precedenti ${title}`;
  }



  const htmlToReactParser = new HtmlToReact.Parser(React);
  const reactReport = htmlToReactParser.parse(report);
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        scroll="paper"
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title" className={classes.title}>
            {title}
        </DialogTitle>
        <DialogContent>
            {reactReport}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Chiudi
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}